@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Global Styling */
* {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

.margins {
  width: 50%;
  padding-bottom: 30px;
  display: block;
  margin: 0 auto;
}

.w-100 {
  width: 100%;
}

.d-block {
  display: block;
}

.pt-30 {
  padding: 30px 0 0 0;
}

.mt-30 {
  margin: 30px 0 0 0;
}

.mt-50 {
  margin: 50px 0 0 0;
}

.pt-110 {
  padding-top: 110px;
}

.pb-10 {
  padding-bottom: 10px;
}

.pl-5 {
  padding-left: 5px;
}

.formHeading {
  width: 100%;
  font-weight: 400;
  font-family: 'Prompt', sans-serif;
  color: #1865f2;
  text-align: center;
  margin-right: 1rem;
}

h2 {
  margin: 0 auto;
  padding: 30px 0 30px 0;
  font-size: 18pt;
  text-align: center;
  font-weight: 300;
}

h3 {
  margin: 0 auto;
  padding: 10px 0 0 0;
  text-align: center;
  font-weight: 300;
}

p {
  font-size: 16px;
  font-weight: 400;
}

.lead {
  font-size: 18px;
  font-weight: 400;
  text-align: center;
  align-items: center;
}

.errorMsg {
  text-align: left;
  float: left;
  margin: 0 0 10px 0;
}

.sucMsg {
  display: block;
  padding: 5px 0 0 0;
  width: 100%;
  text-align: left;
}

a {
  text-decoration: none;
}

.underline {
  text-decoration: underline;
}

.link {
  color: #000;
  font-size: medium;
}

.text-left {
  text-align: left;
}

.red {
  color: #f32013;
}

.blue {
  color: #1b65f2;
}

.greenText {
  color: #4caf50;
}

@media only screen and (max-width: 768px) {
  .margins {
    margin: 0 auto;
    display: block;
    width: 90%;
    justify-content: center;
    justify-self: center;
  }
}

nav {
  height: 60px;
  position: fixed;
  width: 100%;
  z-index: 5;
  /* background: #6668f4; */
  /* background: #2812d1;
  background: -webkit-linear-gradient(top right, #2812d1, #3596cb);
  background: -moz-linear-gradient(top right, #2812d1, #3596cb);
  background: linear-gradient(to bottom left, #2812d1, #3596cb); */
  background-image: linear-gradient(-90deg, #6668f4, #3596cb);
  display: flex;
  color: #fff;
}

.navItems {
  display: flex;
  width: 80%;
  margin: 0 auto;
  /* background: #2812d1;
  background: -webkit-linear-gradient(top right, #2812d1, #3596cb);
  background: -moz-linear-gradient(top right, #2812d1, #3596cb);
  background: linear-gradient(to bottom left, #2812d1, #3596cb); */
  align-items: center;
}

.navItems .logo {
  width: 50%;
  text-align: left;
  font-family: 'Prompt', sans-serif;
  /* color: #1865f2; */
  color: #fff;
  font-size: 20pt;
  font-weight: 400;
}

.navItems ul {
  width: 50%;
  text-align: right;
}

.navItems ul li {
  text-decoration: none;
  display: inline-block;
  /* padding: 10px 10px 10px 10px; */
}

.navItems ul li:hover,
.navItems ul li a:hover {
  cursor: pointer;
}

.navItems ul li a {
  color: white;
  text-decoration: none;
  padding: 10px 10px 10px 10px;
}

.active {
  background-color: #7593f3;
}

.fa-bars,
.fa-times {
  font-size: 20pt;
  display: none;
}

@media only screen and (max-width: 925px) {
  .navItems {
    width: 100%;
  }

  .logo {
    margin-left: 5%;
  }

  .hide {
    display: none;
  }

  .show {
    height: auto;
    display: block;
    transition: all ease 0.5;
    -webkit-transition: all ease 0.5;
    -moz-transition: all ease 0.5;
    -ms-transition: all ease 0.5;
    -o-transition: all ease 0.5;
  }

  .navItems ul {
    z-index: 5;
    position: absolute;
    /* height: auto; */
    width: 100%;
    top: 60px;
    justify-content: center;
    align-items: center;
    /* background: #6668f4; */
    /* background: #2812d1;
    background: -webkit-linear-gradient(top right, #2812d1, #3596cb);
    background: -moz-linear-gradient(top right, #2812d1, #3596cb);
    background: linear-gradient(to bottom left, #2812d1, #3596cb); */
    /* background-image: linear-gradient(-90deg, #2812d1, #3596cb);*/
    background-image: linear-gradient(-90deg, #6668f4, #3596cb);
    border: none;
  }

  .navItems ul li {
    /* display: none; */
    text-decoration: none;
    display: block;
    padding: 20px 0 20px 0;
    text-align: center;
  }

  .fa-bars,
  .fa-times {
    display: block;
    text-align: right;
    width: 100%;
    margin-right: 5%;
  }
}

.modal-wrapper {
  display: block; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 100; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}
.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 50%;
  /* padding: 1rem 1rem; */
}

.modal-body {
  margin: 0 auto;
  width: 100%;
  margin-bottom: 30px;
}

.modal-body h4 {
  font-size: 15pt;
  text-align: center;
  margin: 30px 0 30px 0;
}

.modal-body p {
  font-size: 1rem;
}

.modal-footer {
  width: 100%;
  margin: 0 auto;
  margin-bottom: 30px;
  display: flex;
  justify-content: flex-end;
  font-size: 1rem;
  border: none;
  outline: none;
  cursor: pointer;
}

.modal-footer p {
  padding-right: 10px;
}

.modal-footer form {
  height: auto;
  width: auto;
  margin: 0;
  padding: 0;
}

.modal-btn {
  height: auto;
  margin-top: 0;
  background-color: #fff;
  color: red;
}

@media only screen and (max-width: 768px) {
  .modal-content {
    width: 90%;
  }
}

footer {
  height: 60px;
  position: relative;
  bottom: 0;
  width: 100%;
  background: #6668f4;
  color: white;
  display: flex;
  align-items: center;
}

.footerItems {
  display: flex;
  width: 80%;
  margin: 0 auto;
  background-color: inherit;
  justify-content: center;
  display: block;
}

.footerText {
  font-size: 10pt;
  color: white;
  padding-top: 5px;
  cursor: pointer;
}
form {
  margin-top: 100vh;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 123px;
}

input,
select {
  display: block;
  height: 40px;
  width: 100%;
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0 auto;
  margin-top: 50px;
  outline: none;
  border: none;
  border-bottom: 1px solid #000;
  font-size: 16px;
}

input[type='submit'],
input[type='button'],
button {
  display: block;
  height: 40px;
  outline: none;
  border: none;
  font-size: medium;
  cursor: pointer;
}

.green {
  background-color: #4caf50;
}

.medium {
  background-color: #ffa601;
}

.hard {
  background-color: #f32013;
}

/* Image Preview */
.imagePrevForm {
  padding: 0;
  margin: 50px 0 0 0;
}

.imagePrev {
  height: 125px;
  width: 125px;
  float: left;
  padding: 10px 0 10px 0;
  display: block;
}

.audioPrev {
  height: 40px;
  width: 250px;
  display: flex;
  padding: 10px 0 0 0;
}

.removeContainer {
  width: 100%;
  cursor: pointer;
  display: flex;
  padding-top: 10px;
}

.binIcon {
  float: right;
}

.removeText {
  margin-left: 10px;
}

.coverImage {
  width: 100%;
  height: 100vh;
  background-image: url(/static/media/translation-4620781_1920.7e48201e.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.vertical {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 40vh;
}

section {
  height: 100vh;
  min-width: 100%;
  background-color: #d7eef7;
  justify-content: center;
  align-items: center;
}

section:nth-of-type(2) {
  background-color: #fff;
}

.icons {
  padding-top: 30vh;
  width: 100px;
  height: 100px;
  display: block;
  margin: 0 auto;
}

.spinnerContainer {
    min-height: 100vh;
    width: 100%;
    z-index: 20;
    justify-content: center;
    display: flex;
    align-items: center;
}
.decks {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  padding-top: 30px;
  justify-content: space-evenly;
  margin: 0 auto;
}

img {
  width: 100%;
  height: 275px;
  display: block;
  margin: 0 auto;
}

p {
  text-align: center;
}

.controls {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.controls p {
  margin: 30px 0 30px 0;
}

@media only screen and (min-width: 1076px) {
  @-moz-document url-prefix() {
      .ffStyles {
          padding: 0 15px 0 15px;
      }
  }
}

@media only screen and (max-width: 1100px) {
  .decks {
    width: 100%;
    display: block;
  }
}

.card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
  background: white;
  cursor: pointer;
}

.space-around {
  display: flex;
  justify-content: space-evenly;
  align-content: space-around;
}

@media only screen and (max-width: 768px) {
  .card {
    margin: 0 auto;
    justify-content: center;
    justify-self: center;
    display: block;
    margin-bottom: 30px;
  }
}

.feedback {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.btn-front {
  overflow-x: hidden;
  width: 100%;
  margin: 0 auto;
  height: 74%;
  bottom: 16px;
  display: flex;
  align-items: flex-end;
}

.fileContainer {
  height: 150px;
  width: 250px;
  padding-top: 10px;
  margin: 0 auto;
}

.audio {
  height: 40px;
  width: 250px;
  display: block;
  margin: 0 auto;
}

.cardImg {
  height: 150px;
  width: 250px;
  padding-top: 10px;
}

.bottom {
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
  height: 36%;
  align-items: flex-end;
  bottom: 16px;
}

@media only screen and (max-width: 768px) {
  .feedback {
    display: block;
    padding: 0;
    margin: 0;
  }

  .bottom {
    height: auto;
  }
}

